/* Style buttons placed in different parts of the ui (header, content, footer)*/

#header-container a.btn[aria-expanded="true"] {
    background-color: #0069d9;
    pointer-events: none;
}

#header-container a.btn[aria-expanded="false"] {
    background-color: #138496;
}

.white-text-button {
    border: none; /* Remove borders */
    color: white; /* White text */
    padding: 12px 16px; /* Some padding */
    font-size: 16px; /* Set a font size */
    cursor: pointer; /* Mouse pointer on hover */
}

a:not([href]).white-text-button {
    color: white
}