/* Animating the top property in Microsoft Edge does not work so prevent animating it */
_:-ms-lang(x), _:-webkit-full-screen, .individual-bubble { top: inherit !important }

.custom-context-menu {
    position: fixed;
}

.levelIndicator {
    position: absolute;
    left: 70px;
    top: 120px;
    z-index: 1;
    color: #B0C4DE;
}

.isChargingIndicator {
    /* position: absolute;
    left: 80px;
    top: 99px; */
    z-index: 1;
    font-size: 20px;
    color: #B0C4DE;
}

#dashboardBattery > div.battery.onscreen::after {
    background-color: #1aa9c0;
    content: '';
    display: block;
    height: 10px;
    position: absolute;
    right: 26px;
    top: -15px;
    width: 30px;
    border-top: 1px solid transparent;
    border-bottom: 1px solid transparent;
    border-top-left-radius: 1px;
    border-top-right-radius: 1px;
}

#dashboardBattery > div.battery.onscreen > div {
    width: 85px;
}

#dashboardBattery > div.battery-text.onscreen {
    margin-top: 15px;
    margin-left: 10px;
}