body {
    background: #343E59;
    color: rgb(143, 143, 143);
    font-family: Montserrat, Arial, sans-serif;
}

.content-area {
    max-width: 1280px;
    margin: 0 auto;
}

.box {
    background-color: #2B2D3E;
    padding: 25px 20px;
}

.shadow {
    box-shadow: 0px 1px 15px 1px rgba(69, 65, 78, 0.08);
}

.modal-content {
    background-color: #2B2D3E;
    margin-top: 30px;
}

.custom-font {
    font-family: Helvetica, Arial, sans-serif;
    opacity: 1;
    text-anchor: start;
    dominant-baseline: auto;
    font-size: 14px;
    font-weight: 900;
    fill: rgb(204, 204, 204);
    color: #a5a5a5;
}

.custom-light-weight {
    font-weight: 525;
}
